import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import "./NavBar.css";

interface SubMenu {
    title: string;
    path: string;
}

interface MenuItem {
    title: string;
    path?: string;
    submenu?: SubMenu[];
}

interface NavbarProps {
    menuItems: MenuItem[];
    isLargeFont: boolean;
    isChinese: boolean;
}

const Navbar: React.FC<NavbarProps> = ({ menuItems, isLargeFont, isChinese }) => {
    const [menuOpen, setMenuOpen] = useState(false); // For small screens
    const [hoveredIndex, setHoveredIndex] = useState<number | null>(null); // For submenus
    const navigate = useNavigate();

    const toggleMenu = () => {
        setMenuOpen(!menuOpen);
    };

    const handleLinkClick = (path: string) => {
        setMenuOpen(false); // Close menu on navigation
        navigate(path); // Navigate to the selected path
    };

    const handleSubMenuHover = (index: number | null) => {
        setHoveredIndex(index); // Show/hide submenu on hover
    };

    return (
        <nav className="navbar">
            {/* Hamburger Icon */}
            <div className="hamburger" onClick={toggleMenu}>
                <span className="bar"></span>
                <span className="bar"></span>
                <span className="bar"></span>
            </div>

            {/* Main Menu */}
            <ul className={`menu ${menuOpen ? "menu-open" : ""}`}>
                {menuItems.map((item, index) => (
                    <li
                        key={index}
                        className="menu-item"
                        onMouseEnter={() => handleSubMenuHover(index)}
                        onMouseLeave={() => handleSubMenuHover(null)}
                    >
                        {/* Main Menu Link */}
                        {item.path ? (
                            <Link
                                to={item.path}
                                className="menu-link"
                                onClick={() => handleLinkClick(item.path!)}
                                style={{
                                    fontSize: isLargeFont ? "1.5rem" : "1rem",
                                }}
                            >
                                {isChinese ? item.title : translateTitle(item.title)}
                            </Link>
                        ) : (
                            <span
                                className="menu-link"
                                style={{
                                    fontSize: isLargeFont ? "1.5rem" : "1rem",
                                }}
                            >
                                {isChinese ? item.title : translateTitle(item.title)}
                            </span>
                        )}

                        {/* Submenu */}
                        {item.submenu && hoveredIndex === index && (
                            <ul className="submenu">
                                {item.submenu.map((submenuItem, subIndex) => (
                                    <li key={subIndex} className="submenu-item">
                                        <Link
                                            to={submenuItem.path}
                                            className="submenu-link"
                                            onClick={() => handleLinkClick(submenuItem.path)}
                                            style={{
                                                fontSize: isLargeFont ? "1.2rem" : "1rem",
                                            }}
                                        >
                                            {isChinese
                                                ? submenuItem.title
                                                : translateTitle(submenuItem.title)}
                                        </Link>
                                    </li>
                                ))}
                            </ul>
                        )}
                    </li>
                ))}
            </ul>
        </nav>
    );
};

// Translate function
const translateTitle = (title: string): string => {
    const translations: { [key: string]: string } = {
        "": "Home",
        "": "Biosheng Health Chain Store",
        "": "Our Core Concept",
        "/": "Customer Groups/Events",
        "": "Business Model",
        "()": "Online Store (Future)",
        "AI": "AI Medical Devices",
        "": "Agency Products",
        "": "EndoAngel Products",
        "": "Conference Photos",
        "": "Our Health Products",
        "": "Functional Health Products",
        "": "Assisted Treatment",
        "": "Daily Care",
        "": "Daily Supplements",
        "": "Contact Us",
        "": "About Us",
        "": "Our Products",
    };
    return translations[title] || title;
};

export default Navbar;
