import React from 'react';
import { useLocation } from 'react-router-dom';
import { Breadcrumb } from 'antd';

interface BreadcrumbDisplayProps {
    isChinese: boolean;
}

const BreadcrumbDisplay: React.FC<BreadcrumbDisplayProps> = ({ isChinese }) => {
    const location = useLocation();
    const pathSegments = location.pathname.split('/').filter((segment) => segment);

    const translations: Record<string, string> = {
        '': isChinese ? '' : 'Home',
        'about-us': isChinese ? '' : 'About Us',
        functional: isChinese ? '' : 'Functional Health Products',
        'daily-health': isChinese ? '' : 'Daily Care',
        'core-concept': isChinese ? '' : 'Our Core Concept',
        'customer-events': isChinese ? '' : 'Our Customer Groups and Events',
        'business-model': isChinese ? '' : 'Our Business Model',
        contact: isChinese ? '' : 'Contact Us',
        products: isChinese ? '': 'Health Products',
        '360health': isChinese ? '' : '360health',
        EndoAngel: isChinese ? "":'EndoAngel',
    };

    const breadcrumbItems = [
        { path: '/', label: translations[''] },
        ...pathSegments.map((segment) => ({
            path: `/${segment}`,
            label: translations[segment as keyof typeof translations] || segment,
        })),
    ];

    return (
        <Breadcrumb style={{ margin: '16px 24px' }}>
            {breadcrumbItems.map((item, index) => (
                <Breadcrumb.Item key={index}>
                    {index === breadcrumbItems.length - 1 ? (
                        <span>{item.label}</span>
                    ) : (
                        <a href={item.path}>{item.label}</a>
                    )}
                </Breadcrumb.Item>
            ))}
        </Breadcrumb>
    );
};

export default BreadcrumbDisplay;
